@import "../../utils/variables.scss";
.burger,
.burger-menu__wrapper {
    display: none;
}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width: 0px) and (max-device-width: 1024px) {
    .burger {
        display: block;
        position: fixed;
        top: 10px;
        right: 10px;
        width: 30px;
        height: 20px;
        z-index: 50;
        cursor: pointer;

        &:after,
        &:before {
            content: "";
            background-color: #ffffff;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            transition: all 0.5s ease 0s;
        }
        &:after {
            top: 0;
        }
        &:before {
            bottom: 0;
        }

        &-line {
            background-color: #ffffff;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            top: 9px;
            transition: all 0.5s ease 0s;
        }
    }

    .burger:hover {
        cursor: pointer;
    }

    .burger-active {
        span {
            transform: scale(0, 0);
        }
        &::before {
            transform: rotate(45deg);
            top: 9px;
        }

        &::after {
            transform: rotate(-45deg);
            top: 9px;
        }
    }

    .burger-menu__wrapper {
        display: block;
        position: absolute;
        top: -100%;
        right: 0;
        z-index: 20;
        height: 100%;
        background-color: $nav-color;
        transition: all 0.5s ease 0.2s;
        &-active {
            top: 0;
            transition: all 0.5s ease 0.2s;
        }
    }
}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width: 481px) {
    .burger-menu__wrapper {
        // display: block;
        // position: absolute;
        top: -200%;
        // right: 0;
        // z-index: 20;
        // height: 100%;
        // background-color: $nav-color;
        // transition: all 0.5s ease 0.2s;
        &-active {
            top: 0;
        }
    }
}
