@import "../../utils/variables.scss";

.navigation {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 80%;
    margin-left: 20px;
    &__list {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: 310px;
        margin: 0;
        padding: 10px;
        border-radius: 50px;
        background-color: $nav-color;
    }
    &__item {
        padding: 5px 0;
        list-style-type: none;
    }
    &__link {
        font-size: 2rem;
    }
    &-link__active {
        width: 55px;
        height: 37px;
        display: inline-block;
        background: #04b4e0;
    }

    &__arrows {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: 100px;
        padding: 10px;
        border-radius: 50px;
        background-color: $nav-color;
    }
}

[nav-tooltip] {
    position: relative;
    z-index: 10;
    font-family: "Roboto", sans-serif;
}

[nav-tooltip]::after {
    content: attr(nav-tooltip);
    position: absolute;
    width: auto;
    opacity: 0;
    color: #fff;
    padding: 2px 10px;
    background-color: #04b4e0;
    white-space: nowrap;
    left: 0;
    top: -2.3em;
    margin-top: 50%;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    z-index: 0;
    box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.22);
}

[nav-tooltip]:hover::after {
    opacity: 1;
    left: -6em;
}

[nav-tooltip]:focus:after {
    columns: white;
}

